export default {
	currentScrape(state) {
		return state.currentScrape
	},
	loggedIn(state) {
		return state.loggedIn
	},
	scrapes(state) {
		return state.scrapes
	},
	userId(state) {
		return state.userId
	},
	username(state) {
		return state.username
	}
}



